import React from 'react'
import { rdColor } from 'diana'
import './index.less'

export default class follow extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      col: '#666',
    }
  }

  // 500
  componentDidMount() {
    this.interval = setInterval(this.getRandomColor, 500)
  }

  // 
  componentWillUnmount() {
    clearInterval(this.interval)
  }

  getRandomColor = () => {
    this.setState({
      col: rdColor(),
    })
  }

  render() {
    const { col } = this.state
    return (
      <div className="animated flip" className=".ani-box">
        <div><a href="https://github.com/MuYunyun/react-antd-demo" className="welcome animated flip text" style={{ color: col }}></a></div>
        <img src={require('../../images/face.png')} width="100" alt="logo" className="lastPic" />
        <div className="animated swing discribe"></div>
        <div className="animated swing discribe">demo <a href="https://github.com/MuYunyun/react-antd-demo">Star</a></div>
      </div>
    )
  }
}
